	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"No More Of This","date":"14 Jan 2002","slug":"/2002-01-14-no-more-of-this/","tags":"Y2002"},"html":"<p>I thought that Christopher would only climb out of his crib if a chair was next to it. Just now he proved me wrong.</p>\n<p>Mama now has a new Überboss, from Holland. He's visiting Hamburg on Saturday. </p>\n<p><a href=\"http://radio.weblogs.com/0100464/2002/01/14.html\">My first impressions</a> of Radio Userland 8. Radio may not run on Linux, but <a href=\"http://radio.weblogs.com\">radio.weblogs.com</a> does. Look at the HTTP headers:\n<pre>200 OK\nConnection: close\nDate: Mon 14 Jan 2002 19:28:11 GMT\nAccept-Ranges: bytes\nServer: Apache/1.3.12 (Unix)  (Red Hat/Linux)\nContent-Length: 63691\nContent-Type: text/html\nETag: \"78717-f8cb-3c432b2f\"</pre>"},"next":{"fields":{"title":"Duel of the German Directors","date":"14 Jan 2002","slug":"/2002-01-14-duel-of-the-german-directors/"}},"previous":{"fields":{"title":"Summer Vacation Is Now","date":"14 Jan 2002","slug":"/2002-01-14-summer-vacation-is-now/"}}},{"node":{"fields":{"title":"Duel of the German Directors","date":"14 Jan 2002","slug":"/2002-01-14-duel-of-the-german-directors/","tags":"Y2002"},"html":"<p>Last evening we had an interesting choice of movies on TV: Roland Emmerich's Independence Day on Pro7, Wolfgang Petersen's Air Force One on RTL. With attacks on New York, the White House and the President, I'm not sure how either one would play on US TV at the moment.<br />\nHaving already seen Will Smith do his thang with the aliens, we went with Harrison Ford on the plane. Independence Day would have been more fun :-)</p>"},"next":{"fields":{"title":"Brother Can You Do Some Time?","date":"14 Jan 2002","slug":"/2002-01-14-brother-can-you-do-some-time/"}},"previous":{"fields":{"title":"No More Of This","date":"14 Jan 2002","slug":"/2002-01-14-no-more-of-this/"}}},{"node":{"fields":{"title":"Brother Can You Do Some Time?","date":"14 Jan 2002","slug":"/2002-01-14-brother-can-you-do-some-time/","tags":"Y2002"},"html":"<p><a href=\"http://www.abendblatt.de/contents/ha/news/lokales/html/140102/1214BENA0.HTM\">Hamburger Abendblatt</a>: A pair of brothers made fools of Hamburg justice officials. The younger brother was a pimp sentenced to 2 years prison. His older brother was broke and offered to take his place. Prison officials didn't notice the switch for over 10 months, even though the brother in the cell was a head taller and 20 kg heavier than the brother that was convicted.</p>"},"next":{"fields":{"title":"The Semmelings","date":"13 Jan 2002","slug":"/2002-01-13-the-semmelings/"}},"previous":{"fields":{"title":"Duel of the German Directors","date":"14 Jan 2002","slug":"/2002-01-14-duel-of-the-german-directors/"}}}],"pathPrefix":"","first":false,"last":false,"index":947,"pageCount":1101}}

